$gray: #d8d8d8;
$sidebar-width: 240px;

* {
  box-sizing: border-box; }

html, body, #root {
  height: 100%; }

body {
  margin: 0;
  font-family: 'Alata', sans-serif;
  font-size: 15px;
  color: #3d3d3d;
  background: #6161611f; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

h1,h2,h3,h4,h5,h6 {
  margin: 0; }

::-webkit-scrollbar {
  width: .7em;
  height: 1em; }

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); }

::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey; }

.sidebar {
  background: $gray;
  padding: 12px 20px 12px 12px;
  width: $sidebar-width;
  position: fixed;
  height: 100%;

  &:hover {
    overflow-y: auto;
    padding-right: 10px; }
  h2 {
    margin-bottom: 12px; } }

.item {
  background: white;
  padding: 5px 10px;
  margin: 6px 0;
  border-radius: 4px; }

#content {
  height: 100%;
  overflow-x: auto;
  overflow-y: auto;
  margin-left: $sidebar-width;
  .groups {
    white-space: nowrap;
    display: flex;
    padding: 28px;
    .add-new {
      border: 1px solid #9a9a9a;
      color: #3d3d3d !important;
      border-radius: 6px;
      padding: 6px 10px;
      cursor: pointer; }
    .group {
      width: 220px;
      margin-right: 28px;
      flex-shrink: 0;
      &:last-child {
        padding-right: 28px; }
      .group-header {
        color: white;
        padding: 8px 12px;
        border-radius: 6px;
        h3 {
          margin-bottom: 8px; } }
      .drag-here {
        border: 1px solid #9a9a9a;
        border-radius: 6px;
        padding: 6px 10px; }
      .subgroup {
        background: $gray;
        margin: 8px 0;
        padding: 8px 16px 12px 16px;
        border-radius: 6px;
        h4 {
          margin-bottom: 8px; } } } } }

.search-input {
  background: white;
  display: flex;
  padding: 5px 8px;
  margin-bottom: 16px;
  border-radius: 4px;
  .icon {
    width: 16px;
    margin-right: 8px; }
  input {
    border: none;
    outline: none; } }

.checkbox {
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 32px;
  margin-bottom: 12px;
  margin-bottom: 16px;
  cursor: pointer;
  user-select: none;
  input {
    position: absolute;
    opacity: 0;
    cursor: pointer; }
  span {
    font-size: 14px; }
  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 18px;
    width: 18px;
    border: 2px solid gray;
    &:after {
      content: '';
      position: absolute;
      display: none;
      left: 4px;
      top: 1px;
      width: 4px;
      height: 9px;
      border: solid #333333;
      border-width: 0 2px 2px 0;
      transform: rotate(45deg); } }

  input:checked ~ .checkmark:after {
    display: block; } }
